    props: {
      /**
       * 导航列表选中项
       */
      current: {
        type: Number,
        default: 0
      },
      /**
       * 导航列表
       */
      list: {
        type: Array,
        default: () => {
          return [];
        }
      },
      /**
       * 文字颜色
       */
      fontColor: {
        type: String,
        default: ''
      },
      /**
       * 底部上边线颜色
       */
      borderColor: {
        type: String,
        default: ''
      },
      /**
       * 背景颜色
       */
      backgroundColor: {
        type: String,
        default: ''
      },
      /**
       * 激活文字颜色
       */
      activeFontColor: {
        type: String,
        default: ''
      },
      // 只在激活状态显示文本
      textAuto: {
        type: Boolean,
        default: false
      },
      // 固定到页面底部
      fixed: {
        type: Boolean,
        default: true
      }
    },

    data() {
			
      return {
				listNav: [
					{
						"pagePath": "/pages/index/index",
						"text": "组件", 
						"src": "../../static/home.png",
						"srcSelect": "../../static/homeactive.png"
					},
					{
						"pagePath": "/pages/template/template",
						"text": "模板",
						"src": "../../static/business.png",
						"srcSelect": "../../static/businessactive.png"
					}
				],
        // 选中项
        select: this.current,
      };
    },

    computed: {
      /**
       * 底部导航栏颜色样式
       */
      setColorStyle() {
        let colorStyle = '';
        // 文字颜色
        if (this.fontColor != '') {
          colorStyle += `color:${this.fontColor};`;
        }
        // 上边线颜色
        if (this.borderColor != '') {
          colorStyle += `border-top: 1px ${this.borderColor} solid;`;
        }
        // 背景颜色
        if (this.backgroundColor != '') {
          colorStyle += `background: ${this.backgroundColor};`;
        }
        return colorStyle;
      },
      /**
       * 激活文字样式
       */
      setActiveFontColorStyle() {
        let activeFontColorStyle = '';
        if (this.activeFontColor != '') {
          activeFontColorStyle += `color:${this.activeFontColor};`;
        }
        return activeFontColorStyle;
      }
    },
		onShow() {
			const vm = this;
